/* eslint-disable react-hooks/exhaustive-deps */
import { Fragment, useEffect, useState } from "react";
import { IconButton } from "@mui/material";
import { Switch, MenuItem } from "@material-ui/core";

import { UIButton } from "../../../../Components/exports";
import {
  CustomInputField,
  CustomTextFieldFilled,
} from "../../../../Components/UI/CustomTextField/CustomTextField";

import leftArrow from "../../../../assets/icons/leftArrow.svg";
import Loader from "../../../../shared-components/loader";
import { useToastMessage } from "../../../../contexts/ToastMessageContext";

import TagInput from "../../../../Components/UI/TagInput";

import {
  saveLeadDataField,
  getLDIdDetail,
  updateLeadDataField,
} from "../../../../api/leadDataField";
import { useLeadDataFields } from "../../../../contexts/LeadDataFieldProvider";

import useStyles from "./styles/muiStyles";
//import { getLeadDataFields } from "../../../../api/LMSRules";

const LeadForm = () => {
  const classes = useStyles();
  const {
    allLeadDataFields,
    setAllLeadDataFields,
    editLdfId,
    setShowInviteForm,
  } = useLeadDataFields();
  const { setErrorMessage, setSuccessMessage } = useToastMessage();
  const [removeOptions, setRemoveOptions] = useState(false);

  const [showForm, setShowForm] = useState(false);
  const [data, setData] = useState({
    field_type: "",
    display_name: "",
    field_name: "",
    display_in_filter: false,
    use_lead_title: false,
    field_options: [],
  });
  const {
    field_type,
    display_name,
    field_name,
    display_in_filter,
    use_lead_title,
    field_options,
  } = data;
  const inputChangedHandler = (e) => {
    let value;
    let name = e.target.name;
    value = e.target.value;
    setData({
      ...data,
      [name]: value,
      ...(name === "display_name"
        ? { field_name: value.toLowerCase().replace(" ", "_") }
        : {}),
    });
  };
  const toggleHandler = (e) => {
    let name = e.target.name;
    setData({ ...data, [name]: e.target.checked });
  };
  const submit = async (e) => {
    try {
      e.preventDefault();
      let formData = { ...data };

      if (field_options.length === 0) {
        delete formData.field_options;
      }
      formData.display_in_filter = display_in_filter;

      const res = await saveLeadDataField(formData);
      if (!res.success) {
        setErrorMessage(
          res.messages ? res.messages.errors[0].message : res.message
        );
      } else {
        setSuccessMessage(res.message ?? "Lead Data Field created.");
        setData({
          field_type: "",
          display_name: "",
          field_name: "",
          display_in_filter: false,
          use_lead_title: false,
          field_options: [],
        });
        setRemoveOptions(true);

        const { display_name, field_type, id, field_name, field_options } =
          res.payload;
        setAllLeadDataFields([
          ...allLeadDataFields,
          {
            display_name: display_name,
            field_name: field_name,
            field_type: field_type,
            id: id,
          },
        ]);
        setRemoveOptions(false);

        setShowForm(false);
        setTimeout(() => {
          setShowInviteForm(false);
          window.location.reload();
        }, 200);
      }
    } catch (error) {
      console.error(error.message);
    }
  };

  const update = async (e) => {
    e.preventDefault();
    let formData = { ...data };
    if (field_options.length === 0) {
      delete formData.field_options;
    }
    formData.display_in_filter = display_in_filter;
    console.log({ editLdfId, formData });
    const res = await updateLeadDataField(editLdfId, formData);
    console.log("res", res);
    if (!res.success) {
      setErrorMessage(
        res.messages ? res.messages.errors[0].message : res.message
      );
    } else {
      setSuccessMessage(res.message ?? "Lead Data Field updated.");
      setData({
        field_type: "",
        display_name: "",
        field_name: "",
        display_in_filter: false,
        use_lead_title: false,
        field_options: [],
      });
      setShowForm(false);
      setRemoveOptions(false);

      const { display_name, field_type, field_name, id } = res.payload;

      console.log("display_name", display_name);

      const updatedData = allLeadDataFields.map((fieldData) => {
        if (fieldData.id === id) {
          let updateLD = { ...fieldData };
          updateLD.display_name = display_name;
          updateLD.field_type = field_type;
          updateLD.field_name = field_name;
          return updateLD;
        } else {
          return fieldData;
        }
      });
      setAllLeadDataFields(updatedData);
      setTimeout(() => setShowInviteForm(false), 200);
    }
  };

  function handleSelectedTags(items) {
    setData({ ...data, field_options: items });
  }

  useEffect(() => {
    setTimeout(() => setShowForm(true), 200);
  }, []);

  const getLeadDataFields = async () => {
    const res = await getLDIdDetail(editLdfId);
    if (!res.success) {
      setErrorMessage(
        res.messages ? res.messages.errors[0].message : res.message
      );
    } else {
      setData({
        field_type: res.payload.field_type,
        display_name: res.payload.display_name,
        field_name: res.payload.field_name,
        display_in_filter: res.payload.display_in_filter,
        use_lead_title: res.payload.mark_as_lead_title,
        field_options: res.payload.field_options
          ? res.payload.field_options
          : [],
      });
    }
  };

  useEffect(() => {
    editLdfId && getLeadDataFields();
  }, [editLdfId]);

  return (
    <Fragment>
      <div className={classes.overlay} />
      <div className={`${classes["lead-form"]}${showForm ? " active" : ""}`}>
        <Fragment>
          <div className="header">
            <IconButton
              aria-label="Back"
              className="back-btn"
              onClick={() => {
                setShowForm(false);
                setTimeout(() => setShowInviteForm(false), 200);
              }}
              style={{ color: "transparent", padding: 0 }}
            >
              <img
                alt="back"
                src={leftArrow}
                style={{ margin: "7.5px 10px" }}
              />
            </IconButton>
            <h1 className="title">
              {`${editLdfId ? "Edit" : "Create a"} Data Field`}{" "}
            </h1>
          </div>
          <Loader label="Sending Invite" />

          <form onSubmit={(e) => e.preventDefault()}>
            <div className="field">
              <CustomTextFieldFilled
                select
                placeholder="Select Type"
                label="Data Field Type"
                variant="filled"
                type="select"
                value={field_type}
                fullWidth
                name="field_type"
                onChange={(e) => inputChangedHandler(e)}
                className={classes["selectButtonWrapper"]}
              >
                <MenuItem value="" disabled>
                  {"Select Data Field Type"}
                </MenuItem>
                <MenuItem value="numeric">{"Number"}</MenuItem>
                <MenuItem value="string">{"Text"}</MenuItem>
                <MenuItem value="boolean">{"Boolean"}</MenuItem>
                <MenuItem value="date">{"Date"}</MenuItem>
              </CustomTextFieldFilled>
            </div>
            <div className="field">
              <CustomInputField
                type="text"
                placeholder="Display Name"
                name="display_name"
                value={display_name}
                rows={1}
                onChange={(e) => inputChangedHandler(e)}
              />
            </div>
            <div className="field">
              <CustomInputField
                type="text"
                placeholder="Field Name"
                name="field_name"
                value={field_name}
                rows={1}
                onChange={(e) => inputChangedHandler(e)}
              />
            </div>
            <div className="field">
              {field_options.length > 0 ? (
                <TagInput
                  selectedTags={handleSelectedTags}
                  tags={field_options}
                  resetTag={removeOptions}
                  InputLabelProps={{ shrink: data.field_options.length > 0 }}
                  fullWidth
                  rows={2}
                  variant="filled"
                  id="tags"
                  name="tags"
                  label="Options"
                />
              ) : (
                <TagInput
                  selectedTags={handleSelectedTags}
                  resetTag={removeOptions}
                  InputLabelProps={{ shrink: data.field_options.length > 0 }}
                  fullWidth
                  rows={2}
                  variant="filled"
                  id="tags"
                  name="tags"
                  label="Options"
                />
              )}
            </div>
            <div className="field flex">
              <div className="grow">
                <h5 className="field-title">
                  {"Display Field in Filters & Analytics?"}
                </h5>
                <small className="field-subtitle">
                  {
                    "This field will be displayed in filter option and analytics can be drawn on this field."
                  }
                </small>
              </div>
              <Switch
                classes={{
                  root: classes.root,
                  switchBase: classes.switchBase,
                  thumb: classes.thumb,
                  track: classes.track,
                  checked: classes.checked,
                }}
                name="display_in_filter"
                onChange={(e) => toggleHandler(e)}
                checked={display_in_filter === true ? true : false}
              />
            </div>
            <div className="field flex">
              <div className="grow">
                <h5 className="field-title">{"Use as Lead Title"}</h5>
                <small className="field-subtitle">
                  {
                    "If a value is available in the field it will show as the default title instead of the phone number in the Chat section."
                  }
                </small>
              </div>
              <Switch
                classes={{
                  root: classes.root,
                  switchBase: classes.switchBase,
                  thumb: classes.thumb,
                  track: classes.track,
                  checked: classes.checked,
                }}
                name="use_lead_title"
                onChange={(e) => toggleHandler(e)}
                checked={use_lead_title === true ? true : false}
              />
            </div>
            <div className="actions">
              {editLdfId ? (
                <UIButton
                  type="button"
                  className="ms-0"
                  onClick={(e) => update(e)}
                >
                  {"Update"}
                </UIButton>
              ) : (
                <UIButton
                  type="button"
                  className="ms-0"
                  onClick={(e) => submit(e)}
                >
                  {"Save"}
                </UIButton>
              )}
            </div>
          </form>
        </Fragment>
      </div>
    </Fragment>
  );
};

export default LeadForm;
