import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  "mobile-settings": {
    display: "flex",
    flexDirection: "column",
    minHeight: "100%",
    overflow: "hidden",
    width: "100%",

    "& > .header": {
      alignItems: "center",
      borderBottom: "1px solid #24272c1a",
      display: "flex",
      gap: "20px",
      height: "70px",
      padding: "20px",
      width: "100%",

      "& .back-link": {
        alignItems: "center",
        display: "flex",
        height: "100%",

        "& img": {
          margin: "0 10px",
        },
      },

      "& .title": {
        color: "#24272c",
        fontWeight: 500,
        fontSize: "24px",
        lineHeight: "28px",
      },
    },

    "& .content": {
      flex: "1 0 0",

      "& .lead-container": {
        display: "flex",
        flexDirection: "column",
        // height: "calc(100vh - 70px)",
        height: "100%",

        "& .default-block": {
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          marginTop: "30px",
          padding: "30px",
          rowGap: "24px",

          "& .cta": {
            textAlign: "center",

            "& .btn-action": {
              backgroundColor: "#12B0EE",
              borderRadius: "8px",
              boxShadow: "0px 8px 8px rgba(50, 197, 255, 0.1)",
              fontSize: "16px",
              fontWeight: 700,
              height: "36px",
              lineHeight: "19px",
              margin: 0,
              textTransform: "capitalize",
            },
          },

          "& .info": {
            color: "#24272C",
            textAlign: "center",

            "& p": {
              fontSize: "15px",
              fontWeight: 400,
              lineHeight: "18px",
              opacity: 0.7,

              "& strong": {
                fontWeight: 700,
              },
            },
          },
        },

        "& .card-group": {
          display: "flex",
          flexDirection: "column",
          height: "100%",

          "& .header": {
            display: "flex",
            flexDirection: "column",
            minHeight: "72px",
            justifyContent: "flex-end",
            padding: "0 20px 10px",
            width: "100%",

            "& .below": {
              alignItems: "center",
              display: "flex",
              width: "100%",

              "& .left, & .right": {
                alignItems: "center",
                display: "flex",
                flexShrink: 1,

                "& > a": {
                  alignItems: "center",
                  display: "flex",
                  height: "100%",
                },

                "& h2": {
                  color: "#24272C",
                  fontSize: "18px",
                  fontWeight: 500,
                  lineHeight: "21px",
                  whiteSpace: "nowrap",
                },

                "& .btn-sync": {
                  backgroundColor: "#FFFFFF",
                  borderRadius: "15px",
                  boxShadow: "1px solid rgba(0, 0, 0, 0.2)",
                  fontSize: "14px",
                  fontWeight: 400,
                  height: "30px",
                  lineHeight: "16px",
                  margin: 0,
                  textTransform: "capitalize",

                  "& .MuiButton-label": {
                    whiteSpace: "nowrap",
                    gap: "8px",
                  },
                },
              },

              "& .middle": {
                alignItems: "center",
                display: "flex",
                justifyContent: "flex-end",
                flexGrow: 1,

                "& .search-wrapper": {
                  display: "none",
                  width: "100%",

                  "&.active": {
                    display: "flex",
                  },

                  "& .search-block": {
                    alignItems: "center",
                    display: "flex",
                    width: "100%",
                    position: "relative",
                    marginBottom: "10px",

                    "& .icon": {
                      position: "absolute",
                      left: "5px",
                    },

                    "& .clear": {
                      padding: 0,
                      position: "absolute",
                      right: "10px",

                      "&:hover": {
                        backgroundColor: "transparent",
                      },
                    },

                    "& .search-input": {
                      backgroundColor: "#FFFFFF",
                      border: "1px solid rgba(0, 0, 0, 0.1)",
                      borderRadius: "15px",
                      flexGrow: 1,
                      height: "30px",
                      outline: "none",
                      padding: "0 30px",
                    },
                  },
                },
              },
            },
          },

          "& .card-content": {
            overflowY: "auto",
            flex: "1 0 0",
            display: "flex",
            flexDirection: "column",
            padding: "0 30px",
            gap: "10px",

            "& .MuiListItem-root": {
              paddingTop: "14px",
              paddingBottom: "14px",

              "&:not(:last-child)": {
                borderBottom: "1px solid #24272C1A",
              },

              "& .MuiListItemText-root": {
                margin: 0,

                "& .primary-block": {
                  alignItems: "baseline",
                  display: "flex",
                  gap: "10px",

                  "& .list-title": {
                    color: "#24272C",
                    fontSize: "15px",
                    fontWeight: 500,
                    lineHeight: "18px",
                  },

                  "& .type": {
                    color: "#767676b3",
                    fontSize: "12px",
                    fontWeight: 400,
                    lineHeight: "12px",
                    textTransform: "uppercase",
                  },

                  "& .status-chip": {
                    borderRadius: "4px",
                    height: "20px",

                    "& .MuiChip-label": {
                      color: "#FFFFFF",
                      fontSize: "10px",
                      fontWeight: 900,
                      lineHeight: "12px",
                      padding: "0 4px",
                    },
                  },
                },

                "& .MuiListItemText-secondary": {
                  color: "#24272C",
                  fontSize: "12px",
                  fontWeight: 400,
                  lineHeight: "15px",
                  maxWidth: "230px",
                },
              },

              "& .MuiListItemIcon-root": {
                minWidth: "unset",
              },
            },
          },

          "& .footer": {
            alignItems: "center",
            borderTop: "1px solid #24272c1a",
            display: "flex",
            flexDirection: "column",
            minHeight: "72px",
            justifyContent: "center",
            padding: "10px 20px",
            width: "100%",

            "& .btn-action": {
              backgroundColor: "#FFFFFF",
              border: "1px solid #DEE2EA",
              borderRadius: "8px",
              boxShadow: "0px 8px 8px rgba(50, 197, 255, 0.1)",
              color: "#32C5FF",
              fontSize: "14px",
              fontWeight: 500,
              lineHeight: "16px",
              height: "40px",
              textTransform: "capitalize",
            },
          },
        },
      },
    },
  },

  "item-options": {
    "& .MuiPaper-root": {
      minWidth: "120px",

      "& .MuiList-root": {
        width: "100%",

        "& .MuiMenuItem-root": {
          width: "100%",
        },
      },
    },
  },
  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {
    "lead-container": {
      display: "flex",
      flexDirection: "column",
      paddingBottom: "50px",
      paddingTop: "50px",
      rowGap: "36px",
      maxWidth: "650px",
      width: "100%",

      "& .card-group": {
        backgroundColor: "#ffffff",
        borderRadius: "10px",
        boxShadow: "0px 0px 20px 1px rgba(0, 0, 0, 0.1)",
        padding: "30px 36px 40px",

        "&.default": {
          minHeight: "300px",

          "& .inner-block": {
            display: "flex",
            flexDirection: "column",
            height: "100%",
            justifyContent: "center",
            margin: "0 auto",
            maxWidth: "400px",
            rowGap: "24px",
            width: "100%",

            "& .cta": {
              textAlign: "center",

              "& .btn-action": {
                backgroundColor: "#12B0EE",
                borderRadius: "8px",
                boxShadow: "0px 8px 8px rgba(50, 197, 255, 0.1)",
                fontSize: "16px",
                fontWeight: 700,
                height: "36px",
                lineHeight: "19px",
                margin: 0,
                textTransform: "capitalize",
              },
            },

            "& .info": {
              color: "#24272C",
              textAlign: "center",

              "& p": {
                fontSize: "15px",
                fontWeight: 400,
                lineHeight: "18px",
                opacity: 0.7,

                "& strong": {
                  fontWeight: 700,
                },
              },
            },
          },
        },

        "& .header": {
          display: "flex",
          flexDirection: "column",

          "& .top": {
            display: "flex",
            justifyContent: "end",

            "& .btn-action": {
              color: "#ffffff",
              backgroundColor: "#12B0EE",
              borderRadius: "8px",
              boxShadow: "0px 8px 8px rgba(50, 197, 255, 0.1)",
              fontSize: "16px",
              fontWeight: 700,
              height: "36px",
              lineHeight: "19px",
              margin: 0,
              maxWidth: "175px",
              padding: "0 5px",
              textTransform: "capitalize",
              width: "100%",
            },
          },

          "& .below": {
            alignItems: "center",
            display: "flex",
            marginBottom: "10px",
            marginTop: "10px",

            "& .left, & .right": {
              flexShrink: 1,
              maxWidth: "calc(100% - 96px)",

              "& h2": {
                color: "#24272C",
                fontSize: "18px",
                fontWeight: 500,
                lineHeight: "21px",
                whiteSpace: "nowrap",
                overflow: "hidden",
                textOverflow: "ellipsis",
              },

              "& .btn-sync": {
                backgroundColor: "#FFFFFF",
                borderRadius: "15px",
                boxShadow: "1px solid rgba(0, 0, 0, 0.2)",
                fontSize: "14px",
                fontWeight: 400,
                height: "30px",
                lineHeight: "16px",
                margin: 0,
                textTransform: "capitalize",

                "& .MuiButton-label": {
                  whiteSpace: "nowrap",
                  gap: "8px",
                },
              },
            },

            "& .middle": {
              alignItems: "center",
              display: "flex",
              justifyContent: "flex-end",
              flexGrow: 1,
              maxWidth: "100%",

              "& .search-wrapper": {
                display: "none",
                width: "100%",

                "&.active": {
                  display: "flex",
                },

                "& .search-block": {
                  alignItems: "center",
                  display: "flex",
                  width: "100%",
                  padding: "9px 0",
                  position: "relative",

                  "& .icon": {
                    position: "absolute",
                    left: "5px",
                  },

                  "& .clear": {
                    padding: 0,
                    position: "absolute",
                    right: "10px",

                    "&:hover": {
                      backgroundColor: "transparent",
                    },
                  },

                  "& .search-input": {
                    backgroundColor: "#FFFFFF",
                    border: "1px solid rgba(0, 0, 0, 0.1)",
                    borderRadius: "15px",
                    flexGrow: 1,
                    height: "30px",
                    maxWidth: "100%",
                    outline: "none",
                    padding: "0 30px",
                  },
                },
              },
            },
          },
        },

        "& > .MuiDivider-root": {
          "&.full": {
            margin: "0 -36px",
          },
        },

        "& .card-content": {
          display: "flex",
          flexDirection: "column",
          gap: "10px",

          "& .MuiListItem-root": {
            paddingTop: "14px",
            paddingBottom: "14px",

            "&:not(:last-child)": {
              borderBottom: "1px solid #24272C1A",
            },

            "& .MuiListItemText-root": {
              margin: 0,

              "& .primary-block": {
                alignItems: "baseline",
                display: "flex",
                flexWrap: "wrap",
                gap: "10px",
                marginBottom: "6px",

                "& .list-title": {
                  color: "#24272C",
                  fontSize: "15px",
                  fontWeight: 500,
                  lineHeight: "18px",
                },

                "& .type": {
                  color: "#767676b3",
                  fontSize: "12px",
                  fontWeight: 400,
                  lineHeight: "12px",
                  textTransform: "uppercase",
                },

                "& .status-chip": {
                  borderRadius: "4px",
                  height: "20px",

                  "& .MuiChip-label": {
                    color: "#FFFFFF",
                    fontSize: "10px",
                    fontWeight: 900,
                    lineHeight: "12px",
                    padding: "0 4px",
                  },
                },
              },

              "& .MuiListItemText-secondary": {
                color: "#24272C",
                fontSize: "12px",
                fontWeight: 400,
                lineHeight: "15px",
                maxWidth: "290px",
              },
            },

            "& .MuiListItemIcon-root": {
              minWidth: "unset",
            },
          },
        },

        "& .card-title": {
          color: "#24272C",
          fontSize: "18px",
          fontWeight: 500,
          lineHeight: "21px",
          marginBottom: "30px",
        },

        "& .flex-row": {
          display: "flex",
          columnGap: "35px",

          "&.min-gap": {
            columnGap: "18px",
          },

          "& .half": {
            flex: "0 0 auto",
            width: "calc((100% - 18px)/2)",
          },

          "& .grow-colum": {
            flexGrow: 1,
          },

          "& .shrink-colum": {
            flexShrink: 1,
          },
        },

        "& .field": {
          marginBottom: "30px",
        },
      },

      "& .action": {
        display: "flex",
        justifyContent: "end",
      },
    },
  },
  [theme.breakpoints.up("md")]: {},
  [theme.breakpoints.up("lg")]: {},
  [theme.breakpoints.up("xl")]: {},
}));

export default useStyles;
