/* eslint-disable react-hooks/exhaustive-deps */
import { Fragment, useEffect, useState } from "react";
import { Divider, Button } from "@mui/material";
import { Hidden } from "@material-ui/core";

import { useToastMessage } from "../../../contexts/ToastMessageContext";

import { getAllLeadDataFields } from "../../../api/leadDataField";
import { useLeadDataFields } from "../../../contexts/LeadDataFieldProvider";
import leftArrow from "../../../assets/icons/leftArrow.svg";

import LeadHeader from "./LeadHeader";
import LeadFooter from "./LeadFooter";
import LeadListItem from "./LeadListItem";
import useStyles from "./styles/muiStyles";

const LeadDataFieldContainer = () => {
  const classes = useStyles();
  const [loading, setLoading] = useState(true);
  const { setErrorMessage } = useToastMessage();
  const { allLeadDataFields, setAllLeadDataFields, setShowInviteForm } =
    useLeadDataFields();
  const [search, setSearch] = useState("");
  const businessId = localStorage.getItem("businessId");

  useEffect(() => {
    const preload = async () => {
      const res = await getAllLeadDataFields();
      res && setLoading(false);
      if (!res.success) {
        setErrorMessage(res.message);
      } else {
        setAllLeadDataFields(res.payload);
      }
    };
    preload();
  }, []);

  return (
    <Fragment>
      {/* Mobile View */}
      <Hidden smUp implementation="js">
        <div className={classes["mobile-settings"]}>
          {allLeadDataFields && allLeadDataFields.length === 0 && (
            <div className="header">
              <a href="/business-settings" className="back-link">
                <img alt="back" src={leftArrow} />
              </a>
              <h1 className="title">{"Lead Data Fields"}</h1>
            </div>
          )}
          <div className="content">
            <div className="lead-container">
              {allLeadDataFields && allLeadDataFields.length === 0 ? (
                <div className="default-block">
                  <div className="cta">
                    <Button
                      variant="contained"
                      type="button"
                      className="btn-action"
                      onClick={() => setShowInviteForm(true)}
                    >
                      {"New Lead Data Field"}
                    </Button>
                  </div>
                  <div className="info">
                    <p>
                      {
                        "Lead Data Field creates an input field for your user, where the value input by a user can be stored and be later retrieved for different users to draw an analytical comparison or as a future reference."
                      }
                    </p>
                  </div>
                </div>
              ) : (
                <div className="card-group">
                  <LeadHeader
                    searchLeads={(value) => setSearch(value)}
                    leadsCount={allLeadDataFields?.length ?? 0}
                  />
                  <Divider className="full" />
                  <div className="card-content">
                    {allLeadDataFields &&
                      allLeadDataFields
                        .filter((element) =>
                          element?.display_name
                            ?.toLowerCase()
                            .includes(search?.toLowerCase())
                        )
                        .reverse()
                        .map((e, i) => {
                          return (
                            <LeadListItem
                              key={i}
                              title={e.display_name}
                              description={
                                e.field_type === "string"
                                  ? "Text"
                                  : e.field_type
                              }
                              fieldId={e.fieldId}
                              fieldName={e.field_name}
                              businessId={businessId}
                              isDefault={e.is_default}
                            />
                          );
                        })}
                  </div>
                  <LeadFooter />
                </div>
              )}
            </div>
          </div>
        </div>
      </Hidden>
      {/* Table/Desktop View */}
      <Hidden xsDown implementation="js" only="xs">
        {!loading && (
          <div className={classes["lead-container"]}>
            {allLeadDataFields.length === 0 ? (
              <div className="card-group default">
                <div className="inner-block">
                  <div className="cta">
                    <Button
                      variant="contained"
                      type="button"
                      className="btn-action"
                      onClick={() => setShowInviteForm(true)}
                    >
                      {"New Lead Data Field"}
                    </Button>
                  </div>
                  <div className="info">
                    <p>
                      {
                        "Lead Data Field creates an input field for your user, where the value input by a user can be stored and be later retrieved for different users to draw an analytical comparison or as a future reference."
                      }
                    </p>
                  </div>
                </div>
              </div>
            ) : (
              <div className="card-group">
                <LeadHeader
                  searchLeads={(value) => setSearch(value)}
                  leadsCount={allLeadDataFields?.length ?? 0}
                />
                <Divider />
                <div className="card-content">
                  {allLeadDataFields &&
                    allLeadDataFields
                      .filter((element) =>
                        element?.display_name
                          ?.toLowerCase()
                          .includes(search?.toLowerCase())
                      )
                      .reverse()
                      .map((e, i) => {
                        return (
                          <LeadListItem
                            key={i}
                            title={e.display_name}
                            description={
                              e.field_type === "string" ? "Text" : e.field_type
                            }
                            fieldId={e.id}
                            fieldName={e.field_name}
                            businessId={businessId}
                            isDefault={e.is_default}
                          />
                        );
                      })}
                </div>
              </div>
            )}
          </div>
        )}
      </Hidden>
    </Fragment>
  );
};

export default LeadDataFieldContainer;
