import { Fragment, useState } from "react";
import {
  Menu,
  MenuItem,
  DialogTitle,
  DialogContent,
  DialogActions,
  Dialog,
  Button,
  ListItem,
  ListItemText,
  ListItemIcon,
} from "@mui/material";
import { IconButton } from "@material-ui/core";
import { MoreVert } from "@mui/icons-material";

import { deleteLeadDataField } from "../../../../api/leadDataField";
import { useLeadDataFields } from "../../../../contexts/LeadDataFieldProvider";
import { useToastMessage } from "../../../../contexts/ToastMessageContext";

import useStyles from "../styles/muiStyles";

const LeadListItem = ({
  title,
  description,
  fieldId,
  fieldName,
  businessId,
  isDefault,
}) => {
  const classes = useStyles();
  //contexts
  const {
    allLeadDataFields,
    setAllLeadDataFields,
    setLdfId,
    setShowInviteForm,
  } = useLeadDataFields();
  const { setErrorMessage, setSuccessMessage } = useToastMessage();
  // 3dot menu state
  const [anchorEl, setAnchorEl] = useState(null);
  //dialog state for delete
  const [openConfirmDialog, setOpenConfirmDialog] = useState(false);
  // function for editing ldf
  const handleClickEdit = (fieldId) => {
    setLdfId(fieldId);
    setShowInviteForm(true);
  };
  //function for opening confirm box for delete
  const handleClickDelete = () => {
    setOpenConfirmDialog(true);
  };
  //function for closing confirm box for delete
  const handleClickClose = () => {
    setOpenConfirmDialog(false);
  };
  //function for opening 3 dot menu
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  //function for closing 3 dot menu
  const handleClose = () => {
    setAnchorEl(null);
  };
  //function deleting team member
  const deleteMember = async () => {
    try {
      const res = await deleteLeadDataField(fieldId);
      //handleClose();
      if (!res.success) {
        setErrorMessage(res.message);
      } else {
        setSuccessMessage(res.message);
        const updatedDataFields = allLeadDataFields.filter(
          (dataField) => dataField.id !== fieldId
        );
        setAllLeadDataFields(updatedDataFields);
      }
    } catch (err) {
      handleClose();
      setErrorMessage(err.message);
    }
  };

  return (
    <Fragment>
      <ListItem component="div">
        <ListItemText
          primary={
            <div className="primary-block">
              <h2 className={"list-title"}>{title}</h2>
              <h5 className="type">{description}</h5>
            </div>
          }
          secondary={`[${title.toLowerCase().replace(" ", "_")}]`}
        />
        {!isDefault && (
          <ListItemIcon>
            <IconButton
              // disabled
              onClick={handleClick}
            >
              <MoreVert />
            </IconButton>
            {Boolean(anchorEl) && (
              <Menu
                anchorEl={anchorEl}
                open={Boolean(anchorEl)}
                onClose={handleClose}
                onClick={handleClose}
                keepMounted
                MenuListProps={{
                  "aria-labelledby": "Options",
                }}
                anchorOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
                transformOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
                className={classes["item-options"]}
              >
                <MenuItem
                  onClick={() => {
                    handleClickEdit(fieldId);
                  }}
                >
                  {"Edit"}
                </MenuItem>

                {!isDefault && (
                  <MenuItem
                    onClick={() => {
                      handleClickDelete();
                    }}
                  >
                    {"Delete"}
                  </MenuItem>
                )}
              </Menu>
            )}
          </ListItemIcon>
        )}
      </ListItem>
      <Dialog
        open={openConfirmDialog}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">{"Delete"}</DialogTitle>
        <DialogContent>{"Are you sure you want to delete?"}</DialogContent>
        <DialogActions>
          <Button
            onClick={() => {
              handleClickClose();
            }}
          >
            {"Cancel"}
          </Button>
          <Button
            onClick={async () => {
              await deleteMember();
              handleClickClose();
            }}
            autoFocus
          >
            {"Delete"}
          </Button>
        </DialogActions>
      </Dialog>
    </Fragment>
  );
};

export default LeadListItem;
