import React, { Fragment } from "react";
import { Hidden } from "@mui/material";

import LeadDataFieldContainer from "../../../Container/BusinessSettings/LeadDataFields";
import LeadForm from "../../../Container/BusinessSettings/LeadDataFields/LeadForm";
import { useLeadDataFields } from "../../../contexts/LeadDataFieldProvider";

import Layout from "../Layout";

const LeadDataField = () => {
  const { showInviteForm } = useLeadDataFields();

  return (
    <Fragment>
      <Hidden smUp implementation="js">
        <LeadDataFieldContainer />
        {showInviteForm && <LeadForm />}
      </Hidden>
      <Hidden xsDown implementation="js" only="xs">
        <Layout useFluid>
          <LeadDataFieldContainer />
          {showInviteForm && <LeadForm />}
        </Layout>
      </Hidden>
    </Fragment>
  );
};

export default LeadDataField;
