import { useState } from "react";
import { IconButton, Button } from "@material-ui/core";
import { Search, Clear } from "@material-ui/icons";
import { motion } from "framer-motion";
import { Hidden } from "@mui/material";

import leftArrow from "../../../../assets/icons/leftArrow.svg";
import { useLeadDataFields } from "../../../../contexts/LeadDataFieldProvider";

const LeadHeader = ({ searchLeads, leadsCount }) => {
  const [isActive, setActive] = useState(false);
  const SearchVariant = {
    open: {
      opacity: 1,
    },
    closed: {
      opacity: 0,
    },
  };
  const transition = { duration: 0.4, ease: [0.43, 0.13, 0.23, 0.96] };
  const { setShowInviteForm } = useLeadDataFields();
  const handleSearch = (e) => {
    searchLeads(e.target.value);
  };

  return (
    <div className="header">
      <Hidden xsDown implementation="js" only="xs">
        <div className="top">
          <Button
            variant="contained"
            type="button"
            className="btn-action"
            onClick={() => setShowInviteForm(true)}
          >
            {"New Lead Data Field"}
          </Button>
        </div>
      </Hidden>
      <div className="below">
        {!isActive && (
          <div className="left">
            <Hidden smUp implementation="js">
              <a href="/business-settings">
                <img src={leftArrow} style={{ margin: "10px" }} alt="back" />
              </a>
            </Hidden>
            <h2>{`Lead Data Fields`}</h2>
          </div>
        )}
        <div className="middle">
          <motion.div
            animate={isActive ? "open" : "closed"}
            transition={transition}
            variants={SearchVariant}
            className={`search-wrapper${isActive ? " active" : ""}`}
          >
            <div className="search-block">
              <Search className="icon" />
              <input
                onChange={handleSearch}
                placeholder="Search Fields"
                className="search-input"
              />
              <IconButton
                className="clear"
                onClick={() => {
                  searchLeads("");
                  setActive(!isActive);
                }}
              >
                <Clear />
              </IconButton>
            </div>
          </motion.div>
          {/* Search Icon */}
          {!isActive && (
            <IconButton
              className="search-toggle"
              onClick={() => {
                return setActive((prevState) => !prevState);
              }}
            >
              <Search />
            </IconButton>
          )}
        </div>
      </div>
    </div>
  );
};

export default LeadHeader;
