import { API_V2 } from "../Backend";
import { AuthenticatedHeaders } from "../utils/ApiHeaders";

export const getAllLeadDataFields = async () => {
  try {
    const res = await fetch(`${API_V2}/lead-data-fields`, {
      method: "GET",
      headers: AuthenticatedHeaders(),
    });
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};

export const saveLeadDataField = async (data) => {
  try {
    const res = await fetch(`${API_V2}/lead-data-fields`, {
      method: "POST",
      headers: AuthenticatedHeaders(),
      body: JSON.stringify(data),
    });
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};

export const deleteLeadDataField = async (dataFieldId) => {
  try {
    const res = await fetch(
      `${API_V2}/lead-data-fields/delete/${dataFieldId}`,
      {
        method: "DELETE",
        headers: AuthenticatedHeaders(),
      }
    );
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};

export const getLDIdDetail = async (fieldId) => {
  try {
    const res = await fetch(`${API_V2}/lead-data-fields/edit/${fieldId}`, {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.getItem("token")}`,
        "X-BusinessId": localStorage.getItem("businessId"),
      },
    });
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};

export const updateLeadDataField = async (editLdfId, data) => {
  try {
    const res = await fetch(`${API_V2}/lead-data-fields/update/${editLdfId}`, {
      method: "PUT",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.getItem("token")}`,
        "X-BusinessId": localStorage.getItem("businessId"),
      },
      body: JSON.stringify(data),
    });
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};
